<template>
  <div>
    <raw-loader :mytxt=" require('!raw-loader!./KonvaVueRotate.vue').default "></raw-loader> 
    <h1>This is KonvaVue</h1>
    <b-row>
      <b-col sm="3" class="border-right">
          <div class="mb-1">
          </div>
          <div id="stage-parent">
            <div id="container" class="border">
                <v-stage ref="stage"
                    @click="onStageClick"
                    @mousemove="onStageMouseMove" 
                    @mouseenter="onStageMouseenter"
                    @mouseleave="onStageMouseleave"
                    :config="configKonva" >
                    <v-layer ref="layer">
                        <v-circle ref="circle" :config="configCircle"></v-circle>
                        <v-rect ref="rect" 
                            :config="{
                                x: 100,
                                y: 100,
                                width: 100,
                                height: 100,
                                fill: 'yellow',
                                stroke:'blue',
                                strokeWidth:5,
                                //shadowBlur: 10,
                                draggable:true,
                                visible:false
                            }"
                        />
                        <v-line ref="line" :config="{
                          points: [100,100,400,200],
                          stroke: 'gray',
                          strokeWidth: 5,
                          lineCap: 'round',
                          lineJoin: 'round',
                          draggable:true,
                          visible: false
                        }"></v-line>
                        <v-shape ref="nice"
                          :config="{
                            sceneFunc: function(context, shape) {
                              let x=-20;
                              let y=-50;
                              context.beginPath();
                              context.moveTo(x+20, y+50);
                              context.lineTo(x+220, y+80);
                              context.quadraticCurveTo(x+150, y+100, x+260, y+170);
                              context.closePath();
                              context.fillStrokeShape(shape);
                            },
                            fill: '#00D2FF',
                            stroke: 'black',
                            strokeWidth: 4,
                            visible: true,
                            draggable:true,
                            visible:false,
                          }"
                        />
                        <v-shape ref="ball"
                          :config="{
                            x:150,
                            y:150,
                            r:90,
                            text:'1',
                            fontSize:50,
                            fill: 'aqua',
                            stroke: 'black',
                            textcolor:'red',
                            strokeWidth: 1,
                            draggable:true,  
                            sel:false,      
                            visible:false,
                            sceneFunc: function (context, shape) {
                              let fontSize=shape.getAttr('fontSize');//40;
                              context.beginPath();
                              context.arc(0, 0, shape.getAttr('r'), 0, 2 * Math.PI);
                              context.fillStrokeShape(shape);
                              let textcolor=shape.getAttr('textcolor');
                              context.fillStyle=textcolor;//'white';
                              context.textBaseline = 'middle'; 
                              context.textAlign = 'center';
                              context.font=`${fontSize}px verdana`;//verdana//Times New Roman
                              context.fillText(`${shape.getAttr('text')}`, 0,0);

                            }
                          }"
                        />
                        <v-shape ref="board" :config="{
                          x:50,
                          y:50,
                          width:300,
                          height:100,
                          text:'Thức 101',
                          fontSize:60,
                          fill: 'yellow',
                          stroke: 'red',
                          strokeWidth: 1,
                          draggable:true, 
                          visible:false,       
                          sceneFunc: function (context, shape) {
                            let fontSize=shape.getAttr('fontSize');
                            context.beginPath();
                            context.rect(0, 0, shape.getAttr('width'), shape.getAttr('height'));
                            context.fillStrokeShape(shape);
                            context.fillStyle='red';
                            context.textBaseline = 'middle'; 
                            context.textAlign = 'center';
                            context.font=`${fontSize}px Times New Roman`;
                            context.fillText(`${shape.getAttr('text')}`, shape.getAttr('width')/2,shape.getAttr('height')/2);
                          }
                        }">
                        </v-shape>
                    </v-layer>
                </v-stage>
            </div>
          </div>

      </b-col>
      <b-col sm="9">
        {{msg}}<br>
      </b-col>
    </b-row>
  </div>
</template>
<script>
var sceneWidth = 1500;
var sceneHeight = 1000;
var stage=null;
var layer=null;
export default {
  name: 'Tpl',
  components: {
  },
  computed: {
  	test () {
    	return 'hello'
    },
  },
  data(){
    return{
        msg:"hello",
        configKonva: {
          width: sceneWidth,
          height: sceneHeight
        },
        configCircle: {
          x: sceneWidth/2,
          y: sceneHeight/2,
          radius: 90,
          fill: "yellow",
          stroke: "black",
          strokeWidth: 5,
          draggable:true,
          visible:true,
        },
        count:0,
        p:{},



        
        arPoint:[],
        Sel:[],
        arSel:[],
        unLink:false,
        listData:[],
        hover:-1,
    }
  },
  mounted(){
    this.$nextTick(function(){
      layer=this.$refs.layer.getNode();
      stage=this.$refs.stage.getNode();
      window.addEventListener("resize", this.fitStageIntoParentContainer);
      this.fitStageIntoParentContainer();
      let ami=this.createAnimate();
      ami.start();
    })
  },
  created(){
  },
  methods:{
    createRect(x,y){
      let rect=this.$refs.rect.getNode().clone();
      rect.visible(true);
      rect.x(x-rect.width()/2);
      rect.y(y-rect.height()/2);
      rect.name("myrect");
      return rect;
    },
    createAnimate(){
      let c=this.$refs.circle.getNode();
      let that=this;
      var anim = new Konva.Animation(function (frame) {
        that.getListRect().forEach((x,i)=>{
          let p={
            x:x.x()+x.width()/2,
            y:x.y()+x.height()/2
          }
          //that.rotateAroundPoint(x,2,p);
          //x.rotation(3);
          that.rotateAroundPoint(x,2,{x:c.x(),y:c.y()});
          let p2=that.myGetClientRect(x);
          let p3={x:p2.x+p2.width/2,y:p2.y+p2.height/2}
          that.rotateAroundPoint(x,5,p3);

        })
      }, layer);
      return anim;
    },
    onStageClick(e){
      let p=this.myGetMouse();
      let rect=this.createRect(p.x,p.y);
      layer.add(rect);
    },
    onStageMouseMove(e){
    },
    onStageMouseenter(e){
    },
    onStageMouseleave(e){
    },
    getListRect(){ return layer.find(".myrect") },
    getListLine(){ return layer.find(".myline") },
    getListBall(){ return layer.find(".myball") },
    getBallById(id){ return layer.find(`#${id}`)[0] },
    rotateAroundPoint(shape, angleDegrees, point) {
        let angleRadians = angleDegrees * Math.PI / 180; // sin + cos require radians
        const x =
          point.x +
          (shape.x() - point.x) * Math.cos(angleRadians) -
          (shape.y() - point.y) * Math.sin(angleRadians);
        const y =
          point.y +
          (shape.x() - point.x) * Math.sin(angleRadians) +
          (shape.y() - point.y) * Math.cos(angleRadians);
        shape.position({x: x, y: y});  // move the rotated shape in relation to the rotation point.
        shape.rotation(shape.rotation() + angleDegrees); // rotate the shape in place around its natural rotation point
    },
    myGetClientRect(shape){
        let scale2=stage.scale().x;
        let r=shape.getClientRect();
        r.x=r.x/scale2;
        r.y=r.y/scale2;
        r.width=r.width/scale2;
        r.height=r.height/scale2;
        return r;
    },

    myGetMouse() {
      let scale=stage.scale().x;
      var mousePos = stage.getPointerPosition();
      var x = mousePos.x;
      var y = mousePos.y;
      var xx = Math.round(x / scale);
      var yy = Math.round(y / scale);
      return { x: xx, y: yy };
    },
    fitStageIntoParentContainer() {
      var container = document.querySelector('#stage-parent');
      var containerWidth = container.offsetWidth;
      var scale = containerWidth / sceneWidth;
      stage.width(sceneWidth * scale);
      stage.height(sceneHeight * scale);
      stage.scale({ x: scale, y: scale });
      this.configKonva.width = sceneWidth * scale;
      this.configKonva.height = sceneHeight * scale;
    },
  }
}
</script>
